<template>
<div>
  <h3>Loan of <input type="text" v-model="loan"> eur</h3>
  <h3>
    Calculated with <input type="text" v-model="percent">% interest for <input type="text" v-model="years"> years ({{this.months}} months)
  </h3>

 <chartist :data="cdata" :options="options" type="Line" style="height: 300px;"></chartist>


  <table width="100%">
    <tr>
      <td style="width: 50%">
        <table style="width: 100%">
          <tr>
            <td colspan="4" style="text-align: center; background: #d70206">
              <b>Anuitetas</b>
            </td>
          </tr>
          <tr>
            <th>Mėnuo</th>
            <th>Įmoka</th>
            <th>Kredito dalis</th>
            <th>Procentų dalis</th>
          </tr>
          <tr v-for="(o, i) in annuity">
            <td><b>{{ i + 1 }}</b></td>
            <td>{{ o.payment }}</td>
            <td>{{ o.loanPart }}</td>
            <td>{{ o.percentPart }}</td>
          </tr>
          <tr>
            <td>Viso:</td>
            <td>{{ this.round(totalPaid(annuity)) }}</td>
            <td>{{ this.loan }} </td>
            <td>{{ this.round(totalPercent(annuity)) }}</td>
          </tr>
        </table>
      </td>
      <td style="width: 50%">
        <table style="width: 100%">
          <tr>
            <td colspan="4" style="text-align: center; background: goldenrod">
              <b>Linijinis</b>
            </td>
          </tr>
          <tr>
            <th>Mėnuo</th>
            <th>Įmoka</th>
            <th>Kredito dalis</th>
            <th>Procentų dalis</th>
          </tr>
          <tr v-for="(o, i) in linear">
            <td><b>{{ i + 1 }}</b></td>
            <td>{{ o.payment }}</td>
            <td>{{ o.loanPart }}</td>
            <td>{{ o.percentPart }}</td>
          </tr>
          <tr>
            <td>Viso:</td>
            <td>{{ this.round(totalPaid(linear)) }}</td>
            <td>{{ this.loan }} </td>
            <td>{{ this.round(totalPercent(linear)) }}</td>
          </tr>
        </table>
      </td>
    </tr>
    <tr>
      <td>
        <h3>
          Procentų skirtumas: {{ this.round(totalPercent(annuity) - totalPercent(linear)) }} eur
        </h3>
      </td>
    </tr>
  </table>

  
</div>
</template>

<script>
export default {
  name: 'Calculator',
  data () {
    return {
      percent: 3.0,
      years: 30,
      loan: 100000,
      options: {
        chartPadding: {
          right: 40
        }
      }
    }
  },
  methods: {
    ppmt: function (rate) {
      if (rate === undefined) rate = this.monthlyRate;
      var tmp = Math.pow(1 + rate, this.months);
      return rate / (tmp - 1) * tmp * this.loan;
    },
    round: function (number) {
      return Math.round(number * 100) / 100;
    },
    totalPaid: function (payments) {
      return payments.map(function(p) { return p.payment }).reduce(function(total, p) { return total + p } )
    },
    totalPercent: function (payments) {
      return payments.map(function(p) { return p.percentPart }).reduce(function(total, p) { return total + p } )
    }
  },
  computed: {
    monthlyRate: function () {
      return this.percent / 100 / 12;
    },
    months: function () {
      return this.years * 12
    },
    annuity: function () {
      var payments = [];
      var left = this.loan;
      
      for(var i = 0; i < this.months; i++) {
        var payment = this.round(this.ppmt());
        var percentPart = this.round(left * this.monthlyRate);
        var loanPart = this.round(payment - percentPart);
        left -= loanPart;
        payments.push({payment, loanPart, percentPart});
      }
      
      return payments;
    },
    linear: function () {
      var payments = [];
      var left = this.loan;

      for(var i = 0; i < this.months; i++) {
        var loanPart = this.round(this.loan / this.months);
        var percentPart = this.round(left * this.monthlyRate);
        var payment = this.round(loanPart + percentPart);
        left -= loanPart;
        payments.push({payment, loanPart, percentPart});
      }

      return payments;
    },
    cdata: function () {
      var labels = []
      var series = []
      var series2 = []
      for(var i=1.4;i<5;i+=0.1) {
        var l = this.round(i)
        var rate = i / 100 / 12

        labels.push(l)
        series.push(this.ppmt(rate))

        var payment = this.round((this.loan / this.months) + (this.loan * rate));
        series2.push(payment)
      }
      return {
        labels: labels,
        series: [
          {
            data: series
          },
          {
            data: series2
          }
        ]
      }
    }
  }
}
</script>

<style scoped>
td {
  text-align: left;
  width: 80px;
}
</style>



